let util = {
    toNumberFormat: (num: number) => {
        let temp = Math.floor(num / 1000);
        if (temp > 0) {
            let numStr = num.toString();
            let numArr = [];
            let j = 0;
            for (let i = 3; i < numStr.length; i += 3) {
                numArr.unshift(numStr.substr(-i, 3));
                j=i;
            }
            numArr.unshift( numStr.substr(-numStr.length, numStr.length - j) );
            return numArr.join(",");
        } else {
            return num;
        }
    }
}

export default util;



